.workflow {
  background-color: #EDF9FE;
  margin-bottom: 100px;

  &__title {
    margin: 0 auto 40px;
    text-align: center;
    color: #24385D;
    font-size: 1.5em;
    font-weight: 600;
    opacity: 0;
    transform: translateY(20%);
    transition: opacity 1s ease, transform 1s ease;

    @media (min-width: 1024px) {
      font-size: 3.5em;
    }

    &--animate {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 40px;
    align-items: center;

    @media (min-width: 1024px) {
      flex-direction: row-reverse;
    }
  }

  &__img {
    background-image: url(../../../../../images/workflow.svg);
    background-position: center;
    background-size: cover;
    width: 100%;
    aspect-ratio: 16 / 12;
    flex: 1;
    opacity: 0;
    transform: translateX(-100%);
    transition: opacity 1s ease, transform 2s ease;

    &--animate {
      opacity: 1;
      transform: translateX(0);
    }
  }

  &__descriptions {
    flex: 1;
  }

  &__description {
    opacity: 0;
    transform: translateX(100%);
    transition: opacity 1s ease, transform 2s ease;

    &--animate {
      opacity: 1;
      transform: translateX(0);
    }
  }


  &__subtitle {
    margin: 0;
    color: #24385D;
    font-weight: 500;
    font-size: 1.125em;
    position: relative;
    padding-left: 20px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 12px;
      height: 20px;
      background-image: url('../../../../../images/lightning.svg');
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &__text {
    margin: 0 0 20px;
    color: #8795B0;
    font-size: 1.125em;
  }
}

[dir='rtl'] .workflow__subtitle {
  padding-right: 20px;
}

[dir='rtl'] .workflow__subtitle::before {
  right: 0;
}

[dir='rtl'] .workflow__img {
  transform: translateX(100%);
}

[dir='rtl'] .workflow__img--animate {
  transform: translateX(0);
}

[dir='rtl'] .workflow__description {
  transform: translateX(-100%);
}

[dir='rtl'] .workflow__description--animate {
  transform: translateX(0);
}
