.introBlock {
  display: flex;
  flex-direction: column;
  margin-bottom: 200px;
  gap: 20px;
  background-color: #EDF9FE;
  transition: margin-top 0.6s ease;
  animation: slideUp 1s ease-out forwards, appear 1s;


  @media (min-width: 1024px) {
    padding-top: 40px;
    flex-direction: row;
    gap: 40px;
    align-items: center;
    top: 100px;
  }


  &--topMargin {
    margin-top: 360px;
    transition: margin-top 0.6s ease;
  }

  &__textContent {
    @media (min-width: 1024px) {
      flex: 1;
    }
  }

  &__title {
    margin: 30px auto 40px;
    text-align: center;
    color: #24385D;
    font-size: 1.5em;
    font-weight: 600;

    @media (min-width: 1024px) {
      font-size: 3.5em;
      text-align: start;
    }
  }

  &__text {
    font-size: 0.875em;
    font-weight: 500;
    color: #8795B0;
    text-align: center;
    margin-bottom: 30px;

    @media (min-width: 1024px) {
      font-size: 2em;
      text-align: start;
      margin-bottom: 40px;
    }
  }

  &__social {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 50px;

    @media (min-width: 1024px) {
      justify-content: start;
    }
  }

  &__icon {
    width: 30px;
    height: 30px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50px;
    transition: transform 0.5s, box-shadow 0.5s;

    &:hover {
      transform: scale(120%);
      box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
    }

    @media (min-width: 1024px) {
      width: 40px;
      height: 40px;
    }

    &--telegram {
      background-image: url(../../../../../images/telegram.svg);
    }

    &--whatsapp {
      background-image: url(../../../../../images/whatsapp.svg);
    }

    &--facebook {
      background-image: url(../../../../../images/facebook.svg);
    }

    &--viber {
      background-image: url(../../../../../images/viber.svg);
    }

    &--gmail {
      background-image: url(../../../../../images/gmail.svg);
    }

    &--phone {
      background-image: url(../../../../../images/phone.svg);
    }
  }

  &__img {
    background-image: url('../../../../../images/home-img.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    aspect-ratio: 16 / 11;

    @media (min-width: 1024px) {
      flex: 1;
      width: 100%;
      aspect-ratio: 16 / 11;
    }
  }
}

@keyframes slideUp {
  0% {
    transform: translateY(20%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
