.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 0 18px;
  animation: slideDown 1s ease-out forwards;

  @media (min-width: 1024px) {
    padding: 0 100px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 10px 0 30px;

    @media (min-width: 1024px) {
      margin-top: 20px;
    }
  }

  &__signature {
    background-image: url(../../images/signature.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 40px;
    width: 280px;
    cursor: pointer;
  }

  &__navPhone {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 20px;
    width: 20px;

    &--menu {
      background-image: url(../../images/menu.svg);
      transition: background-image 0.3s ease;
    }

    &--cross {
      background-image: url(../../images/cross.svg);
      transition: background-image 0.3s ease;
    }

    @media (min-width: 1024px) {
      display: none;
    }
  }

  &__menuPhone {
    position: absolute;
    top: 80px;
    left: 18px;
    right: 18px;
    opacity: 0;
    animation: appear 0s linear 1.5s forwards;

    @media (min-width: 1024px) {
      display: none;
    }
  }

  &__listMenuPhone {
    margin: 0;
    padding: 0;
  }

  &__navDesktop {
    display: none;

    @media (min-width: 1024px) {
      display: flex;
      align-items: center;
    }
  }

  &__item {
    list-style: none;
    margin-bottom: 30px;
    text-decoration: none;

    @media (min-width: 1024px) {
      margin-bottom: 0;
      font-size: 1.25em;
    }
  }

  &__link {
    text-decoration: none;
    color: #24385D;

    @media (min-width: 1024px) {
      padding: 20px;
      cursor: pointer;
      border-radius: 5px;
      transition: background-color 0.3s;

      &:hover {
        background-color: #a6e1fa;
      }
    }
  }
}

@keyframes slideDown {
  0% {
    transform: translateY(-20%);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes appear {
  to {
    opacity: 1;
  }
}
